<template><section><h1>GroundPolylinePrimitive</h1>
<p>The <code v-pre>vc-primitive-polyline-ground</code> component is used to load a a GroundPolylinePrimitive represents a polyline draped over the terrain or 3D Tiles in the Scene. Only to be used with GeometryInstances containing GroundPolylineGeometry.</p>
<h2>Example</h2>
<h3>Load GroundPolylinePrimitive with <code v-pre>vc-primitive-polyline-ground</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-polyline-ground :appearance="appearance" :geometryInstances="geometryInstances"></vc-primitive-polyline-ground>
        <vc-primitive-polyline-ground :appearance="appearance">
          <vc-instance-geometry>
            <vc-geometry-polyline-ground :positions="positions" :width="2"></vc-geometry-polyline-ground>
          </vc-instance-geometry>
        </vc-primitive-polyline-ground>
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-layer-imagery>
          <vc-provider-imagery-mapbox mapId="mapbox.streets"></vc-provider-imagery-mapbox>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
